import{getWindow}from'ssr-window';var browser;function calcBrowser(){var window=getWindow();function isSafari(){var ua=window.navigator.userAgent.toLowerCase();return ua.indexOf('safari')>=0&&ua.indexOf('chrome')<0&&ua.indexOf('android')<0;}return{isEdge:!!window.navigator.userAgent.match(/Edge/g),isSafari:isSafari(),isWebView:/(iPhone|iPod|iPad).*AppleWebKit(?!.*Safari)/i.test(window.navigator.userAgent)};}function getBrowser(){if(!browser){browser=calcBrowser();}return browser;}export{getBrowser};